.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  align-items: center;
  display: flex;
}

* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

body, h1, h2, h3, h4, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

.nav__bar {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__links {
  text-transform: uppercase;
  color: #060b35;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  transition: color .3s ease-in-out;
  display: flex;
}

.nav__links:hover {
  color: #00acdc;
  text-decoration: none;
}

.nav__bar > .nav__list {
  margin-right: 20px;
}

.nav__bar > .nav__list:last-child {
  margin: 0;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars__star:last-child {
  margin-right: 4px;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  margin: 0;
  display: grid;
}

.catalog__content {
  grid-template-columns: repeat(1, 200px);
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog__content {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog__content {
    grid-template-columns: repeat(3, 200px);
    padding-inline: 40px;
  }
}

@media (width >= 488px) {
  .catalog__content {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  padding: 32px 16px 16px;
  transition: transform .3s ease-in-out;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  aspect-ratio: 160 / 134;
  flex-shrink: 0;
  width: 160px;
  height: auto;
  margin: 0 auto 40px;
}

.card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__review {
  text-align: right;
  color: #060b35;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  padding: 11px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s ease-in-out;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__review-text {
  justify-self: center;
  align-items: center;
  display: flex;
}

.card:hover .card__name {
  color: #34568b;
}

